.questionInnerWrap {
  font-family: Raleway;
  background-color: #ffffff;
  box-sizing: border-box;
  margin: 0 6.25em;
  padding: 4em;
  box-shadow: 0px 8px 24px rgba(74, 4, 67, 0.19);
  border-radius: 12px;
}
@media only screen and (max-width: 880px) {
  .questionInnerWrap {
    margin: 0;
  }
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap {
    padding: 1.5em;
  }
}
.questionInnerWrap .sentFormTitle {
  font-size: 2.5em;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  color: #482c42;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .sentFormTitle {
    font-size: 1.75em;
  }
}
.questionInnerWrap .title {
  font-size: 2.5em;
  font-weight: 700;
  color: #482c42;
  text-align: center;
}
@media only screen and (max-width: 880px) {
  .questionInnerWrap .title {
    font-size: 2em;
  }
}
@media only screen and (max-width: 360px) {
  .questionInnerWrap .title {
    font-size: 1.7em;
  }
}
.questionInnerWrap .subtitle {
  width: 100%;
  max-width: 520px;
  display: flex;
  font-size: 0.88em;
  color: #865e7e;
  font-weight: 400;
  margin: 2em auto 0;
}
.questionInnerWrap .form {
  margin-top: 2.5em;
  display: flex;
  flex-direction: column;
  row-gap: 1.5em;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .form {
    margin-top: 1.5em;
    row-gap: 1em;
  }
}
.questionInnerWrap .inputsBlock {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  row-gap: 1.5em;
  justify-content: space-between;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .inputsBlock {
    flex-direction: column;
    row-gap: 1em;
  }
}
.questionInnerWrap .submitForm {
  max-width: 45%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .submitForm {
    max-width: 100%;
    row-gap: 1.5em;
  }
}
.questionInnerWrap .buttonComponent {
  margin: auto 0 0;
  background-color: #fec601;
  color: #040404;
  font-size: 0.875em;
  font-weight: 700;
  padding: 1em 0;
  transition: all 0.3s linear;
  box-shadow: 2px 4px 6px rgba(37, 48, 149, 0.25);
  border: none;
}
.questionInnerWrap .buttonComponent:active:not(:disabled), .questionInnerWrap .buttonComponent:hover:not(:disabled) {
  color: #040404;
  background-color: #dfa700;
}
.questionInnerWrap .message {
  display: flex;
  margin-top: 2em;
  font-size: 1.5em;
  justify-content: center;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .message {
    font-size: 1em;
  }
}
.questionInnerWrap .submitImageWrap {
  display: flex;
  margin-top: 3.5em;
  justify-content: center;
}
.questionInnerWrap .submitImage {
  display: flex;
  width: 63px;
  height: 63px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.questionInnerWrap .input,
.questionInnerWrap .textField {
  max-width: 45%;
  color: #482c42;
}
@media only screen and (max-width: 640px) {
  .questionInnerWrap .input,
  .questionInnerWrap .textField {
    max-width: 100%;
  }
}